@font-face {
    font-family: webfont;
    src: url("/webfont/webfont.eot");
    src: url("/webfont/webfont.eot?#iefix") format("embedded-opentype"), url("/webfont/webfont.woff2") format("woff2"), url("/webfont/webfont.woff") format("woff"), url("/webfont/webfont.ttf") format("truetype"), url("/webfont/webfont.svg#webfont") format("svg");
    font-style: normal;
    font-weight: 400;
}

%fi {
    display: inline-block;
    transform: translate(0, 0);
    text-rendering: auto;
    font: normal normal 400 14px/1 webfont;
    font-size: inherit;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
}

.fi {
    @extend %fi;
}

.fi-lg {
    vertical-align: -15%;
    line-height: 0.75em;
